body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-main {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.nav-bar {
  background-color: bisque;
}

.nav-bar ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  list-style: none;
}

.nav-bar ul li {
  margin: 1vmin;
}

.btn-style {
  color: lightyellow;
  border-radius: 5px;
  background: teal;
  -webkit-transition: 0.3s color ease-in-out, 0.3 background ease-in-out;
  transition: 0.3s color ease-in-out, 0.3 background ease-in-out;
  margin-top: 20px;
}

p {
  font-size: 18px;
}

.App-icon {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 15%;
}

.About {
  background-color: teal;
  color: bisque;
}

.App-photo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 15%;
}

.contactsList {
  background-color: teal;
  list-style: none;
}

.card {
  background-color: teal;
  color: bisque;
}

.projects {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  background-color: teal;
  color: bisque;
}

.project-card {
  width: 20%;
  border: 1vmin rgb(97, 226, 97) dotted;
  background-color: bisque;
  border-radius: 2vmin;
  height: 20vmin;
  margin: 3vmin;
}

